html,
body,
#root {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  height: 100%;
  font-family: "Roboto", sans-serif;
  color: rgb(0, 0, 0);
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
input,
textarea {
  border-style: none;
}
button {
  cursor: pointer;
  border-style: none;
}
ul,
ol,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}
a {
  text-decoration: none;
  color: #000;
}
