@import "../../scss/mixins";
.title {
  @include fontPoppins(500, normal);
  font-size: 4em;
}
@media (max-width: 670px) {
  .title {
    font-size: 3em;
  }
}
@media (max-width: 500px) {
  .title {
    font-size: 2em;
  }
}
@media (max-width: 400px) {
  .title {
    font-size: 1.5em;
  }
}
